body {
  color: #40514e;
  margin: 0;
  text-align: center;
  font-family: "Montserrat", sans-serif;
}
hr {
  border-style: none;
  border-top-style: dotted;
  border-width: 15px;
  width: 10%;
  border-color: #e6d261;
  margin: 100px auto;
}
h1 {
  color: #67645d;
  font-size: 3rem;
  margin: 50px auto 0 auto;
  font-family: "Montserrat", sans-serif;
}
h2 {
  color: #40514e;
  font-family: "Montserrat", sans-serif;
  font-size: 2.5rem;
  font-weight: normal;
  padding-bottom: 10px;
}
h3 {
  color: #40514e;
  font-family: "Montserrat", sans-serif;
}
p {
  line-height: 2.2;
  padding: 40px 50px;

}
a {
  font-size: large;
  color: #1d1814;
  font-family: "Montserrat", sans-serif;
  margin: 10px 20px;
  text-decoration: none;
}

a:hover{
    color: #EAF6F6;
}

.skill-row {
  width: 70%;
  margin: 100px auto 150px auto;
  text-align: left;
}

.top-container {
  background-color: #e4f9f5;
  padding-top: 100px;
}
.middle-container {
  margin: 100px 0;
}

.bottom-container {
    background-color: #ffc107;
    padding: 50px 0 5px;
}

.top-cloud {
  position: absolute;
  top: 50px;
  right: 450px;
}
.bottom-cloud {
  position: absolute;
  left: 450px;
  bottom: 200px;
}

.robot-image {
  width: 38%;
  float: left;
  margin-right: 50px;
}
.computer-image {
  float: right;
  margin-left: 30px;
  width: 38%;
}

.Intro {
  width: 30%;
  margin: auto;
}

.Contact-message{
    width: 40%;
    margin: 40px auto 60px;
}


.btn {
    background: #f2b02c;
    background-image: -webkit-linear-gradient(top, #efa141, #f2b02c);
    background-image: -moz-linear-gradient(top, #efa141, #f2b02c);
    background-image: -ms-linear-gradient(top, #efa141, #f2b02c);
    background-image: -o-linear-gradient(top, #efa141, #f2b02c);
    background-image: linear-gradient(to bottom, #efa141, #f2b02c);
    -webkit-border-radius: 8;
    -moz-border-radius: 8;
    border-radius: 8px;
    font-family: "Montserrat", sans-serif;
    color: #ffffff;
    font-size: 20px;
    padding: 10px 20px 10px 20px;
    text-decoration: none;
  }
  
  .btn:hover {
    background: #e6d261;
    background-image: -webkit-linear-gradient(top, #e6d261, #d07922);
    background-image: -moz-linear-gradient(top, #e6d261, #d07922);
    background-image: -ms-linear-gradient(top, #e6d261, #d07922);
    background-image: -o-linear-gradient(top, #e6d261, #d07922);
    background-image: linear-gradient(to bottom, #e6d261, #d07922);
    text-decoration: none;
  }

  .copyright {
    color: #EAF6F6;
    font-size: 0.75rem;
    padding: 20px 0;
  }

  .contenedor{
    background-color: rgb(255, 255, 255);
    border: 0;
  }

  .logo{
  margin: 100px 50px 50px 50px ;
  float: left;
  }
  .texto-1{
    padding-left: 0;
  }